<template>
  <div class="new-address">
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />
    <van-address-edit
      :area-list="addressList"
      :address-info="addressInfo"
      :show-set-default="isDefault"
      :show-delete="isDelete"
      :show-postal="isPostal"
      save-button-text="保存地址"
      delete-button-text="删除地址"
      detail-rows="2"
      detail-maxlength="100"
      @save="saveAddress"
      @delete="deleteAddress"
    />
  </div>
</template>

<script>
import { mapState } from "vuex";

import { utils } from "../utils/utils";
export default {
  name: "NewAddress",
  data() {
    return {
      //初始化组件内容  编辑地址需要aid，新增地址不需要
      addressInfo: {
        aid: "",
        name: "",
        tel: "",
        province: "",
        city: "",
        county: "",
        addressDetail: "",
        areaCode: "",
        postalCode: "",
        isDefault: false,
      },
    };
  },
  created() {
    if (!this.isLogin) {
      this.$router.push({ name: "Login" });
      return;
    }
    //截取查询数据
    let query = this.$route.query;

    //根据地址aid获取地址数据
    if (query.isAdd == 0) {
      //获取token字符串
      let tokenString = localStorage.getItem("_t");
      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "正在加载...",
      });
      this.axios({
        method: "GET",
        url: "/findAddressByAid",
        params: {
          appkey: this.appkey,
          tokenString,
          aid: query.aid,
        },
      })
        .then((d) => {
          this.$toast.clear();

          let data = d.data.result[0];
          data.isDefault = Boolean(data.isDefault);
          for (let key in this.addressInfo) {
            this.addressInfo[key] = data[key];
          }

          //拷贝地址数据
          let addressData = Object.assign({}, this.addressInfo);
          this.$store.commit("newAddressModule/changeData", {
            key: "temporaryAddressData",
            value: addressData,
          });
        })
        .catch((err) => {
          this.$toast.clear();
        });
    }

    this.$store.commit("newAddressModule/changeData", {
      key: "isAdd",
      value: query.isAdd,
    });
    this.$store.commit("newAddressModule/changeData", {
      key: "aid",
      value: query.aid,
    });
    this.$store.commit("newAddressModule/changeData", {
      key: "isDelete",
      value: !query.isAdd,
    });
    this.$store.commit("newAddressModule/changeData", {
      key: "title",
      value: query.isAdd == 0 ? "编辑地址" : "新增地址",
    });
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
    ...mapState("newAddressModule", [
      "isAdd",
      "aid",
      "isDefault",
      "isDelete",
      "isPostal",
      "addressList",
      "title",
      "temporaryAddressData",
    ]),
  },
  methods: {
    //返回
    back() {
      this.$router.go(-1);
    },

    //判断当前地址数据和编辑前的数据是否一样
    isSame(o1, o2) {
      //o1当前，。o2之前
      for (let key in o2) {
        if (o2[key] != o1[key]) {
          //存在数据不一样
          return false;
        }
      }
      return true;
    },

    //保存地址
    saveAddress(addressContent) {
      //addressContent 地址内容

      delete addressContent.country;
      //拷贝对象
      let data = Object.assign({}, addressContent);

      let url = "";
      if (this.isAdd == 1) {
        //新增地址
        delete data.aid;
        url = "/addAddress";
      } else {
        //编辑地址
        //先判断当前地址数据和编辑前的地址数据是否一样
        //如果一样则不请求
        if (this.isSame(addressContent, this.temporaryAddressData)) {
          return this.$router.push({ name: "Address" });
        } else {
          //数据不一样，发起请求
          url = "/editAddress";
        }
      }

      //获取token字符串
      let tokenString = localStorage.getItem("_t");
      data.tokenString = tokenString;
      data.appkey = this.appkey;

      //讲isDefault转为number
      data.isDefault = Number(data.isDefault);

      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "正在加载...",
      });

      this.axios({
        method: "POST",
        url,
        data,
        transformRequest: utils.transformRequest,
      })
        .then((d) => {
          this.$toast.clear();
          // this.$router.push({name:'Address'})
          this.back();
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //删除地址
    deleteAddress() {
      //获取token字符串
      let tokenString = localStorage.getItem("_t");

      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "正在加载...",
      });

      this.axios({
        method: "POST",
        url: "/deleteAddress",
        data: {
          appkey: this.appkey,
          tokenString,
          aid: this.aid,
        },
        transformRequest: utils.transformRequest,
      })
        .then((d) => {
          this.$toast.clear();

          this.$router.push({ name: "Address" });
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>

<style lang="less" scoped>
.new-address {
  padding-top: 46px;
  /deep/ .van-button--danger {
    background-color: #1989fa;
    border-color: #1989fa;
  }
}
</style>